import {createHydrogenContext} from '@shopify/hydrogen';
// Use relative imports so Node can resolve these at runtime in serverless environments.
import {AppSession} from './session.js';
import {CART_QUERY_FRAGMENT} from './fragments.js';

// Define the additional context object
const additionalContext = {
  // Additional context for custom properties, CMS clients, 3P SDKs, etc.
  // These will be available as both context.propertyName and context.get(propertyContext)
  // Example of complex objects that could be added:
  // cms: await createCMSClient(env),
  // reviews: await createReviewsClient(env),
};

/**
 * Creates Hydrogen context for React Router 7.9.x
 * Returns HydrogenRouterContextProvider with hybrid access patterns
 * @param {Request} request
 * @param {Env} env
 * @param {ExecutionContext} executionContext
 */
export async function createHydrogenRouterContext(
  request,
  env,
  executionContext,
) {
  console.log('_env', env.SESSION_SECRET)
  /**
   * Open a cache instance in the worker and a custom session instance.
   */
  // if (!env?.SESSION_SECRET) {
  //   throw new Error('SESSION_SECRET environment variable is not set');
  // }

  const waitUntil = executionContext.waitUntil.bind(executionContext);
  console.log('waitUntil', waitUntil)
  const [cache, session] = await Promise.all([
    caches.open('hydrogen'),
    AppSession.init(request, [env.SESSION_SECRET]),
  ]);

  const hydrogenContext = createHydrogenContext(
    {
      env,
      request,
      cache,
      waitUntil,
      session,
      // Or detect from URL path based on locale subpath, cookies, or any other strategy
      i18n: {language: 'EN', country: 'US'},
      cart: {
        queryFragment: CART_QUERY_FRAGMENT,
      },
    },
    additionalContext,
  );

  return hydrogenContext;
}

/** @typedef {Class<additionalContext>} AdditionalContextType */
